* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 1400px;
    margin: 0 auto;
}

h2 {
    font-size: 20px;
}

a {
    color: black;
    text-decoration: none;
}

p {
    margin-top: 10px;
}

ul {
    display: flex;
    flex-wrap: wrap;
}

li {
    list-style: none;
    margin-right: 20px;
    margin-bottom: 5px;
}

.listbox {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.infobox {
    width: 600px;
    min-height: 300px;
    position:relative;
    padding: 20px 20px 20px 250px;
    border-radius: 15px;
    background-color: aliceblue;
    margin: 20px;
}